footer {
    width: 100%;
    padding: 20px 0px;
    background-color: lightcoral;
    word-wrap: break-word;
}

footer h2 {
    font-size: 3rem;
    color: white;
    cursor: pointer;
}

footer .bottom-nav {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    text-decoration: none;
    list-style: none;
    padding: 0px;
}

.bottom-nav li {
    padding: 10px 0px;
}

.bottom-nav li a {
    font-size: 20px;
    text-decoration: none;
    color: black;
    font-weight: 500;
}

.bottom-nav li a:hover {
    color: white;
}


@media screen and (max-width: 1000px){
    footer h2 {
        font-size: 2.5rem;
    }

    footer .bottom-nav li a {
        
    }
}

@media screen and (max-width: 700px){
    footer h2 {
        font-size: 2rem;
    }

    footer .bottom-nav li a {
        font-size: 18px;
    }
}

@media screen and (max-width: 450px){
    footer h2 {
        font-size: 1.5rem;
    }

    footer .bottom-nav li a {
        font-size: 16px;
    }

    footer p {
        font-size: 14px;
    }
}

