.contact-us {
    width: 100%;
    padding: 0px;
}

.contact-us h2 {
    font-size: 3rem;
    color: brown;
    border-bottom: 1px solid brown;
   width: 60%;
   margin: 20px auto;
}

.contact-us p {
    font-size: 1.2rem;
    width: 80%;
    margin: auto;
}

.contact-us .contact-us-cont {
    width: 90%;
    margin: 100px auto;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    gap: 20px;
    background-color: whitesmoke;
    padding: 20px;
    border-radius: 10px;
    box-shadow: rgba(0, 0, 0, 0.25) 0px 14px 28px, rgba(0, 0, 0, 0.22) 0px 10px 10px;
}

.contact-us-cont a {
    width: 60%;
    z-index: 0;
}

.contact-us-cont a img {
    width: 100%;
}

.contact-us-cont a img:hover {
    filter: brightness(0.5);
}

.contact-us-cont .contact-us-cont-info {
    width: 40%;
}

.contact-us-cont-info h3 {
    font-size: 1.5rem;
    color: brown;
    text-decoration: underline;
}

.contact-us .social_navs {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-evenly;
    width: 100%;
    padding: 0px;
    margin: 80px auto;
}

.contact-us .social_navs li {
    list-style: none;
}

.social_navs li a {
    color: black;
    text-decoration: none;
}

.social_navs li a i {
    font-size: 5rem;
}

.social_navs li a:hover {
    color: brown
}

@media screen and (max-width: 1000px){
    .contact-us h2 {
        font-size: 2.5rem;
    }

    .contact-us p {
        font-size: 1.1rem;
    }

    .contact-us .contact-us-cont {
        flex-direction: column;
    }

    .contact-us-cont .contact-us-cont-info {
        width: 100%;
    }

    .contact-us-cont-info p {
        word-wrap: break-word;
    }

    .contact-us-cont a {
        width: 100%;
    }

    .contact-us .social_navs li a i {
        font-size: 4rem;
    }

}

@media screen and (max-width: 700px){
    .contact-us h2 {
        font-size: 2rem;
    }

    .contact-us p {
        font-size: 1rem;
    }

    .contact-us .contact-us-cont {
        padding: 10px;
    }

    .contact-us-cont .contact-us-cont-info h3{
        font-size: 1.2rem;
    }

    .contact-us .social_navs li a i {
        font-size: 3rem;
    }
    
}

@media screen and (max-width: 450px){
    .contact-us h2 {
        font-size: 1.5rem;
    }

    .contact-us p {
        font-size: 0.9rem;
    }

    .contact-us .contact-us-cont {
        padding: 6px;
    }

    .contact-us-cont .contact-us-cont-info h3{
        font-size: 1rem;
    }

    .contact-us-cont-info p {
        font-size: 14px;
    }

    .contact-us .social_navs li a i {
        font-size: 2rem;
    }
    
}