.App .interactions {
    width: 100%;
    margin: 10px auto;
    padding: 0px;
}

.interactions h2 {
    font-size: 3rem;
    color: brown
}

.interactions p {
    font-size:1.2rem;
    width: 70%;
    margin: 5px auto;
}

.interactions .slider {
    width: 70%;
    padding: 0px;
    margin: 80px auto;
}

.slider img {
    object-fit: contain;
    width: 100%;
    margin:10px auto;
}

@media screen and (max-width: 1000px){
    .interactions h2 {
        font-size: 2.5rem;
    }

    .interactions p {
        font-size: 1.1rem;
    }
}

@media screen and (max-width: 700px){
    .interactions h2 {
        font-size: 2rem;
    }

    .interactions p {
        font-size: 1rem;
    }
    
}

@media screen and (max-width: 450px){
    .interactions h2 {
        font-size: 1.5rem;
    }

    .interactions p {
        font-size: 0.9rem;
    }

    .interactions .slider {
        width: 80%;
    }
    
}