.about-us {

}

.about-us h2 {
    font-size: 3rem;
    color: brown;
    border-bottom: 1px solid brown;
   width: 60%;
   margin: 20px auto;
}

.about-us .about-us-cont {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: flex-start;
    width: 100%;
    padding: 0px;
    gap: 20px;
    margin: auto;

}

.about-us-cont img {
    width: 45%;
}

.about-us-cont .about-us-info {
    width: 45%;
    text-align: center;
    padding: 0px 20px;
}

.about-us-info h3 {
    font-size: 2.2rem;
    color: brown;
}

.about-us-info p {
    font-size: 1rem;
}

.about-us-info button {
    font-size: 1.2rem;
    padding: 10px 10px;
    cursor: pointer;
    color: white;
    background-color: brown;
}

.about-us-info button:hover {
    color: black;
}

@media screen and (max-width: 1000px){
    .about-us h2 {
        font-size: 2.5rem;
    }

    .about-us .about-us-cont .about-us-info h3 {
        font-size: 2.1rem;
    }

    .about-us-info p {
        font-size: 1rem;
    }

    .about-us button {
        font-size: 1.2rem;
        padding: 8px 8px;
    }

    .about-us .about-us-cont {
        flex-direction: column;
        align-items: center;
        width: 100%;
    }

    .about-us-cont img {
        width: 80%;
    }

    .about-us-cont .about-us-info {
        width: 80%;
    }
}

@media screen and (max-width: 700px){
    
}

@media screen and (max-width: 450px){
    .about-us h2 {
        font-size: 2rem;
    }

    .about-us .about-us-cont img {
        width: 90%;
    }

    .about-us .about-us-cont .about-us-info h3 {
        font-size: 1.7rem;
    }

    .about-us-info p {
        font-size: 0.9rem;
    }

    .about-us button {
        font-size: 1.1rem;
        padding: 6px 8px;
    }
    
}



