.App nav {
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
    background-color: lightcoral;
    margin: 0px auto;
    padding: 10px 0px;
    position: fixed;
    top: 0;
    z-index: 1;
}

nav img {
    width: 100px;
    height: 90px;
    border-radius: 40%;
    cursor: pointer;
}

nav ul {
    padding: 0px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}

nav ul li {
    list-style: none;
    margin-right: 10px;
    padding: 10px 8px;
    border: 2px solid transparent;
}

nav ul li:hover {
    border-top: 2px solid brown;
    border-bottom: 2px solid brown;
}
nav ul li:hover a{
    color: white;
}

nav ul li a {
    font-size: 20px;
    text-decoration: none;
    color: black;
}

nav .bar {
    display: none;
}

nav .bar span {
    font-size: 2.25rem;
    cursor: pointer;
    color: white;
    background-color: brown;
    padding: 2px;
}

nav ul .contact {
    border-radius: 5px;
    border: 2px solid brown;
    background-color: brown;
    color: whitesmoke;
}

nav ul .contact:hover {
    color: black;
}

@media screen and (max-width: 1000px) {
    .App nav {
        flex-direction: column;
    }
}

@media screen and (max-width: 700px){
    .App nav {
        flex-direction: row;
        padding: auto 10px;
    }

    .App nav img {
        width: 80px;
        height: 70px;
    }

    nav ul {
        background-color: lightcoral;
        flex-direction: column;
        position: absolute;
        opacity: 0;
        left: -1000px;
        top: 70px;
        right: 0;
        padding: 10px 0px;
        transition: all 0.6s ease-in-out;
    }

    nav ul li {
        margin: 10px auto;
    }

    nav .bar {
        display: flex;
    }

    nav .bar span {
        font-size: 2.1rem;
    }
}

@media screen and (max-width: 450px){

    .App nav img {
        width: 60px;
        height: 50px;
    }

    .App nav ul li a {
        font-size: 1rem;
    }

    nav .bar span {
        font-size: 1.9rem;
    }
}

