header {
    box-shadow: 1px 1px 20px burlywood inset;
    background: url("../images/newHeading.webp");
    background-position:center;
    background-repeat: no-repeat;
    background-size: cover;
    background-clip: ;
    width: 100%;
    margin: 0px;
    margin-top: 100px;
    padding: 10px 0px;
    height: 90vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    text-align: center;
}

header h2 {
    font-size: 4rem;
    text-shadow: 2px 2px 5px brown;
    font-family: "Courgette";
    margin: 0px;
    color: black;
    line-height: 75px;
    margin-top: 50px;
    text-align: center;
}

header h2 span {
    font-size: 4.5rem;
    color: brown;
}

header p {
    font-size: 1.3rem;
    color: black;
    text-align: center;
}

header button {
    font-size: 1.5rem;
    padding: 10px 20px;
    color: black;
    background-color: lightcoral;
    border-radius: 10px;
    color: black;
    text-align: left;
    cursor: pointer;
    transition: all 0.5s ease;
}

header button:hover {
    color: white;
    transform: scale(1.03);
}

@media screen and (max-width:1000px){

    header {
        background-position: top;
        margin-top: 170px;
    }

    header #main_heading {
        font-size: 3rem;
        line-height: 60px;
    }

    header #main_heading span {
        font-size: 4rem;
    }

    header p {
        font-size: 1.1rem;
        word-wrap: break-word;
    }

    header button {
        font-size: 1.5rem;
        padding: 8px 15px;
    }
}

@media screen and (max-width: 700px){

    header {
        padding: 0px;
        margin: 0px;
        background-position: top;
        height: 100vh;
    }


    header #main_heading {
        font-size: 2.3rem;
        line-height: 50px;
    }

    header #main_heading span {
        font-size: 2.7rem;
    }

    header p {
        font-size: 1rem;
    }

    header button {
        font-size: 1.25rem;
        padding: 7px 10px;
    }
    
}

@media screen and (max-width: 450px) {

    header {
        
    }
    header #main_heading {
        font-size: 1.7rem;
        line-height: 40px;
        padding: 0px;
    }

    header #main_heading span {
        font-size: 2rem;
    }

    header p {
        font-size: 0.9rem;
        padding: 0px;
    }

    header button {
        font-size: 1.1rem;
        padding: 6px 8px;
        margin: 0px;
    }
    
}