.paragraph {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    margin: 30px auto;
}

.paragraph h2 {
    font-size: 3rem;
    color: brown;
    width: 60%;
    border-bottom: 1px solid brown;
    margin: auto;
}

.paragraph .paragraph-cont {
    width: 80%;
    margin: 10px auto;
    padding: 10px;
}

.paragraph-cont img {
    width: 40%;
    margin: auto;
    clip-path: ellipse(45% 45% at 50% 50%);
}

.paragraph-cont h3 {
    font-size: 2rem;
    color: brown;
    margin-bottom: 0px;
}

.paragraph-cont h4 {
    font-size: 1.2rem;
    margin-top: 0px;
}

.paragraph-cont p {
    font-size: 1rem;
}

.paragraph-cont span {
    font-size: 2rem;
}

@media screen and (max-width: 1000px){
    .paragraph h2 {
        font-size: 2.5rem;
    }

    .paragraph .paragraph-cont img {
        width: 50%;
    }
}

@media screen and (max-width: 700px){
    .paragraph h2 {
        font-size: 2rem;
    }

    .paragraph .paragraph-cont img {
        width: 60%;
    }

    .paragraph-cont h3 {
        font-size: 1.5rem;
    }

    .paragraph-cont h4 {
        font-size: 1.1rem;
    }

    .paragraph-cont span {
        font-size: 1.5rem;
    }
}

@media screen and (max-width: 450px){
    .paragraph {
        width: 95%;
    }
    .paragraph h2 {
        font-size: 1.5rem;
    }

    .paragraph .paragraph-cont img {
        width: 70%;
    }

    .paragraph-cont h3 {
        font-size: 1.2rem;
    }

    .paragraph-cont h4 {
        font-size: 1rem;
    }

    .paragraph-cont p {
        font-size: 0.9rem;
    }

    .paragraph-cont span {
        font-size: 1.2rem;
    }
}