.services {
    width: 100%;
    padding: 20px 0px;
    margin: 0px;
}

.services h2 {
    font-size: 3rem;
    color: brown;
    border-bottom: 1px solid brown;
   width: 60%;
   margin: 20px auto;
}

.services #services-info {
    font-size: 1.25rem;
    padding: 0px 100px;
}

.services ul {
    display: flex;
    padding: 0px;
    width: 95%;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 20px;
    margin: 20px auto;
    margin-top: 50px;
}

.services button {
    font-size: 1.25rem;
    color: black;
    background-color: lightcoral;
    padding: 10px 15px;
    border-radius: 5px;
    cursor: pointer;
}

.services button:hover {
    color: white;
}

.services ul div {
    padding: 10px 20px;
    border-radius: 10px;
    background-color: whitesmoke;
    box-shadow: rgba(0, 0, 0, 0.25) 0px 25px 50px -12px;
}

.services ul div h4{
    font-size: 25px;
    color: brown;
}

.services ul div span {
    font-size: 8rem;
    color: lightcoral;
}

.services ul div p {
    font-size: 16px;
}

@media screen and (max-width: 1000px){
    .services h2 {
        font-size: 2.5rem;
    }

    .services #services-info {
        font-size: 1.1rem;
        padding: 0px 50px;
    }

    .services button {
        font-size: 1.1rem;
        padding: 9px 13px;
    }

    .services ul {
        display: grid;
        grid-template-columns: auto auto;
    }
}

@media screen and (max-width: 700px){
    .services h2 {
        font-size: 2rem;
    }

    .services #services-info {
        font-size: 1rem;
        padding: 0px 20px;
    }

    .services button {
        font-size: 1rem;
        padding: 8px 12px;
    }

    .services ul div h4 {
        font-size: 20px;
        margin: 10px auto;
    }

    .services ul div p {
        font-size: 15px;
        margin: 0px
    }

    .services ul div span {
        font-size: 7rem;
    }
}

@media screen and (max-width: 500px){
    .services h2 {
        font-size: 1.5rem;
    }

    .services #services_info {
        font-size: 0.9rem;
        padding: 15px;
    }

    .services button {
        font-size: 0.9rem;
        padding: 7px 10px;
    }

    .services ul {
        grid-template-columns: auto;
    }
}
