.gallery h2 {
    font-size: 3rem;
    color: brown;
    border-bottom: 1px solid brown;
   width: 60%;
   margin: 20px auto;
}

.gallery p {
    font-size: 1.2rem;
    width: 80%;
    margin: auto;
}

.gallery .img-cont {
    width: 95%;
    margin: 0px auto;
    display: grid;
    grid-template-columns:  auto auto;
    align-items: center;
    justify-content: center;
    gap: 30px;
}

.img-cont img {
    width: 100%;
}

.img-cont #first {
   
}

.img-cont #second {
    padding-top: 300px;
    
}

.img-cont #third {
   margin-top: -250px;
}

.img-cont #fourth {
    padding-top: 80px;
}

@media screen and (max-width: 1000px){
    .gallery h2 {
        font-size: 2.5rem;
    }

    .gallery p {
        font-size: 1.1rem;
    }
}

@media screen and (max-width: 700px) {
    .gallery h2 {
        font-size: 2rem;
    }

    .gallery p {
        font-size: 1rem;
    }

    .img-cont {
        gap: 20px;
    }

    .img-cont #first {
   
    }
    
    .img-cont #second {
        padding-top: 100px;
        
    }
    
    .img-cont #third {
       margin-top: -100px;
    }
    
    .img-cont #fourth {
        padding-top: 30px;
    }
}

@media screen and (max-width: 450px) {
    .gallery h2 {
        font-size: 1.5rem;
    }

    .gallery p {
        font-size: 0.9rem;
        width: 90%;
    }

    .gallery .img-cont {
        gap: 10px
    }

    .img-cont #first {
   
    }
    
    .img-cont #second {
        padding-top: 60px;
        
    }
    
    .img-cont #third {
       margin-top: -60px;
    }
    
    .img-cont #fourth {
        padding-top: 20px;
    }

}



