.products {
    margin: 10px auto;
    padding: 0px;
}

.products h2 {
    font-size: 3rem;
    color: brown;
   border-bottom: 1px solid brown;
   width: 60%;
   margin: 20px auto;
}

.products .products-cont {
    width: 90%;
    padding: 0px;
    margin: 10px auto;
}

.products #info {
    font-size: 1.2rem;
    width: 85%;
    margin: auto;
}

.products-cont .single-product {
    width: 300px;
    background-color: whitesmoke;
}

.products-cont .single-product img {
    width: 100%;
}

.single-product h3 {
    font-size: 1.5rem;
    color: brown;
}

.single-product p {
    font-size: 1.2rem;
}

.products-cont h4 {
    text-align: center;
    font-size: 3rem;
    color: black;
}

.products-cont h4 span {
    font-size: 2.5rem;
    text-align: center;
    color: brown;
    margin: 0px 15px;
    padding: 0px;
    border: 2px solid brown;
    border-radius: 50%;
    cursor: pointer;
}

.products-cont .typeProduct {
    width: 100%;
    padding: 0px;
}

.products-cont .typeProduct .typeProductCont {
    display: grid;
    grid-template-columns: auto auto auto;
    align-items: center;
    justify-content: space-between;
    gap: 30px;
}


@media screen and (max-width: 1000px) {
    .products h2 {
        font-size: 2.5rem;
    }

    .products #info {
        font-size: 1.1rem;
    }

    .products-cont h4 {
        font-size: 2.5rem;
    }

    .products .products-cont .typeProduct .typeProductCont {
        grid-template-columns: auto auto;
        justify-content: space-evenly;
    }
}

@media screen and (max-width: 700px) {
    .products h2 {
        font-size: 2rem;
    }

    .products #info {
        font-size: 1rem;
    }

    .products-cont h4 {
        font-size: 2rem;
    }
    
    .products .products-cont .single-product{
        width: 195px;

    }
}

@media screen and (max-width: 450px) {
    .products h2 {
        font-size: 1.5rem;
    }

    .products #info {
        font-size: 0.9rem;
    }

    .products-cont h4 {
        font-size: 1.5rem;
    }

    .products .products-cont {
        width: 100%;
    }

    .products .products-cont .single-product {
        width: 80%;
        margin: auto;
    }
    
    .products .products-cont .typeProduct .typeProductCont {
        grid-template-columns: auto;
    }
}
